@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.6em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #333333;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}
.container.margin-top {
  margin-top: 180px;
}

h1 {
  font-weight: 700;
  font-size: 42px;
  margin-bottom: 30px;
}

h2 {
  font-weight: 700;
  font-size: 30px;
  margin-bottom: 30px;
}

p {
  font-size: 22px;
  margin-bottom: 10px;
}

.inner-space {
  padding: 70px 80px;
}

.inner-space-no-bg {
  padding: 20px 80px 0;
}

.outer-space {
  margin: 60px 0;
}

header {
  height: 120px;
  display: flex;
  align-items: center;
  background-color: #EFF2F5;
  position: fixed;
  top: 0;
  width: 100%;
}
header .container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
header .container .logo {
  display: flex;
  align-items: center;
  margin: 0 40px;
}
header .container .logo img {
  height: 100px;
}
header .container nav {
  margin: 0 40px;
  display: flex;
  align-items: center;
}
header .container nav ul {
  list-style-type: none;
  display: flex;
}
header .container nav ul li {
  padding: 0 10px;
  margin: 0 10px;
}
header .container nav ul li a {
  font-size: 20px;
  font-weight: 700;
}
header .container nav ul li a:hover {
  color: #0D73FC;
}

.hero {
  height: 600px;
  scroll-margin-top: 180px;
  position: relative;
  overflow: hidden;
  z-index: -1;
}
.hero .slides {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  transition: left 1s ease-out;
}
.hero .slides .slide {
  display: flex;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
}
.hero .slides .slide img {
  object-fit: cover;
  object-position: center;
  width: 100%;
}
.hero .bars {
  display: flex;
  justify-content: center;
  position: absolute;
  top: 93%;
  left: 50%;
  transform: translateX(-50%);
}
.hero .bars .bar-wrapper {
  padding: 10px;
  cursor: pointer;
}
.hero .bars .bar-wrapper .bar {
  height: 3px;
  background-color: #fff;
  width: 70px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}
.hero .bars .bar-wrapper .bar.active {
  background-color: #0D73FC;
}

.about {
  min-height: 500px;
  background-color: #0D73FC;
  color: #fff;
  scroll-margin-top: 180px;
}
.about h1 {
  color: #fff;
}
.about p {
  color: #fff;
}

.offer {
  min-height: 800px;
  scroll-margin-top: 180px;
}
.offer .offer-container {
  min-height: 400px;
  margin: 60px 0;
  display: flex;
  gap: 40px;
}
.offer .offer-container .picture {
  width: 50%;
  overflow: hidden;
}
.offer .offer-container .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.offer .offer-container .text {
  width: 50%;
  padding: 15px 20px;
}
.offer .offer-container:nth-of-type(2n) {
  flex-direction: row-reverse;
}

.contact {
  min-height: 800px;
  scroll-margin-top: 180px;
}
.contact .contact-container {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
}
.contact .contact-container .form {
  width: calc(50% - 30px);
  min-height: 400px;
  z-index: -1;
}
.contact .contact-container .form form {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.contact .contact-container .form form input,
.contact .contact-container .form form textarea {
  border: 1px solid #82868A;
  padding: 8px 12px;
  font-size: 16px;
  width: 100%;
}
.contact .contact-container .form form .input-container50 {
  width: calc(50% - 10px);
  position: relative;
}
.contact .contact-container .form form .input-container100 {
  width: 100%;
  position: relative;
}
.contact .contact-container .form form textarea[name=message] {
  width: 100%;
  height: 160px;
  line-height: 1.2em;
}
.contact .contact-container .form form button {
  padding: 10px 40px;
  font-size: 16px;
  background-color: #0D73FC;
  color: #fff;
  margin-top: 20px;
  margin-left: auto;
  cursor: pointer;
  border: 1px solid #fff;
  transition: all 0.1s;
}
.contact .contact-container .form form button:hover {
  background-color: #fff;
  color: #0D73FC;
  border: 1px solid #0D73FC;
}
.contact .contact-container .form form p.error {
  margin: 0;
  margin-top: -2px;
  margin-left: 5px;
  font-size: 12px;
  color: red;
  display: none;
  position: absolute;
}
.contact .contact-container .form form p.active {
  display: block;
}
.contact .contact-container .form form input.error,
.contact .contact-container .form form textarea.error {
  border-color: red;
}
.contact .contact-container .address-data {
  width: calc(50% - 30px);
  min-height: 400px;
  background-color: #0D73FC;
  padding: 30px 40px;
}
.contact .contact-container .address-data h2 {
  color: #fff;
}
.contact .contact-container .address-data .address-line {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.contact .contact-container .address-data .address-line a,
.contact .contact-container .address-data .address-line p {
  color: #fff;
  display: block;
  font-size: 22px;
  margin-bottom: 0;
  transition: all 0.2s;
}
.contact .contact-container .address-data .address-line a:hover {
  letter-spacing: 0.5px;
}
.contact .contact-container .address-data .address-line .img-container {
  display: flex;
  justify-content: center;
  width: 60px;
}
.contact .contact-container .address-data .address-line .img-container img {
  height: 26px;
}
.contact .contact-container .map {
  width: 100%;
  min-height: 600px;
}

footer {
  min-height: 100px;
  background-color: #25272A;
}
footer .top-footer .container {
  display: flex;
  padding: 40px;
  justify-content: space-between;
}
footer .top-footer .container .top-footer-box {
  width: 230px;
  display: flex;
  justify-content: center;
}
footer .top-footer .container .logo-socials {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
footer .top-footer .container .logo-socials img {
  height: 100px;
  justify-content: left;
}
footer .top-footer .container .logo-socials .socials {
  margin-top: 15px;
  display: flex;
  gap: 16px;
}
footer .top-footer .container .logo-socials .socials img {
  height: 20px;
  cursor: pointer;
  transition: scale 0.1s ease-out;
}
footer .top-footer .container .logo-socials .socials img:hover {
  scale: 1.5;
}
footer .top-footer .container .contact-footer h3 {
  color: #82868A;
  font-weight: 700;
  margin-bottom: 16px;
  font-size: 22px;
}
footer .top-footer .container .contact-footer a,
footer .top-footer .container .contact-footer p {
  color: #82868A;
  display: block;
  font-size: 18px;
  margin-bottom: 0;
  font-weight: 300;
}
footer .top-footer .container .contact-footer a:hover {
  color: #0D73FC;
}
footer .top-footer .container .menu-footer h3 {
  color: #82868A;
  font-weight: 700;
  margin-bottom: 16px;
  font-size: 22px;
}
footer .top-footer .container .menu-footer ul {
  list-style-type: none;
}
footer .top-footer .container .menu-footer a,
footer .top-footer .container .menu-footer p {
  color: #82868A;
  display: block;
  font-size: 18px;
  margin-bottom: 0;
  font-weight: 300;
}
footer .top-footer .container .menu-footer a:hover {
  color: #0D73FC;
}
footer .bottom-footer {
  border-top: 1px solid #313234;
  height: 60px;
  display: flex;
  align-items: center;
}
footer .bottom-footer .container {
  display: flex;
  padding: 0 40px;
  justify-content: space-between;
  width: 100%;
}
footer .bottom-footer .container .copyrights {
  color: #82868A;
}
footer .bottom-footer .container .regulations {
  color: #82868A;
}
footer .bottom-footer .container .regulations a {
  color: #82868A;
}
footer .bottom-footer .container .regulations a:hover {
  color: #0D73FC;
}